import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Edificio, EntradaPaginacionU, EspacioFisico, Nivel, Ubicacion } from '../../interfaces/Ubicacion';
import { ServiceUbicacionService } from '../../service/service-ubicacion.service';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import Swal from 'sweetalert2';
import { ServiceEdificioService } from '../../service/service-edificio.service';
import { DataTableDirective } from 'angular-datatables';
import { Subject } from 'rxjs';


@Component({
  selector: 'app-listar-ubicacion',
  templateUrl: './listar-ubicacion.component.html',
  styleUrls: ['./listar-ubicacion.component.scss']
})
export class ListarUbicacionComponent implements OnInit {
  breadCrumbItems: Array<{}>;
  ubicaciones: Ubicacion[] = []
  ubicacion: Ubicacion
  ubicacionSeleccionada!: Ubicacion
  edificios: Edificio[] = []
  edificio: Edificio
  formulario !: FormGroup
  butttonText: string
  niveles: Nivel[] = [];
  espacios: EspacioFisico[] = [];
  showLoading = false;

  /**DataTables */
  @ViewChild(DataTableDirective, { static: false }) dtElement: DataTableDirective
  dtOptions: any = {};
  dtTrigger: Subject<Ubicacion> = new Subject<Ubicacion>();


  constructor(

    private ubicacionService: ServiceUbicacionService,
    private edificioService: ServiceEdificioService,
    private modalService: NgbModal,
    private fb: FormBuilder

  ) {
    this.formulario = this.initFomulario();
    this.llenarEdificio();
  }

  ngOnInit(): void {
    this.breadCrumbItems = [{ label: 'Ubicación' }, { label: 'Listado', active: true }];
    this.dtOptions = {
      columnDefs: [
        { className: "", targets: [0, 1, 2, 3, 4] },
        { orderable: false, targets: [4] },
        { searchable: false, targets: [4] },
        /*         { width: "35%", targets: [1,3] },
                { width: "10%", targets: [0, 2, 4] }, */
        { responsivePriority: 1, targets: -1 },
      ],
      lengthMenu: [5, 10, 20, 50],
      destroy: true,
      language: {
        url: '//cdn.datatables.net/plug-ins/1.13.5/i18n/es-ES.json',
        lengthMenu: "Mostrar _MENU_ registros",
        zeroRecords: "Ningún dato encontrado",
      },
      pagingType: 'full_numbers',
      responsive: true,
    };
    this.cargarDatos();
    this.llenarEdificio();

  }

  ngOnDestroy() {
    this.dtTrigger.unsubscribe()
    // this.dtTrigger.complete()
  }


  openModal(content: any, reset: boolean) {
    if (reset) {
      this.ubicacionSeleccionada = null;
      this.butttonText = "Guardar";
      this.formulario.reset()
      this.modalService.open(content,{backdrop: 'static', keyboard: false});
    } else {
      this.llenarNivel(this.ubicacionSeleccionada.edificio.niveles)
      this.butttonText = "Editar";
      this.modalService.open(content,{backdrop: 'static', keyboard: false});
    }
  }

  reset() {
    this.formulario.reset()
  }

  private initFomulario(): FormGroup {
    return this.fb.group({
      edificio: ['', [Validators.required]],
      nivel: ['', [Validators.required]],
      espacio: ['', [Validators.required, Validators.minLength(4)]],

    })
  }

  esCampoValido(campo: string) {
    const validarCampo = this.formulario.get(campo)
    return !validarCampo?.valid && validarCampo?.touched
      ? 'is-invalid' : validarCampo?.touched ? 'is-valid' : '';
  }

  cargarDatos() {
    this.ubicacionService.ubicaciones().subscribe((resp: any) => {
      this.ubicaciones = resp;
      this.dtTrigger.next(null)
    });

  }


  reload() {
    this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
      this.ubicaciones = []
      dtInstance.destroy()
      this.cargarDatos()
    })
  }

  eliminar(ubicacion: Ubicacion) {
    Swal.fire({
      title: '¿Está seguro de eliminar? ',
      text: `Esto no se puede revertir`,
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: "#a90000",
      cancelButtonColor: '#343a40',
      confirmButtonText: 'Sí, borrar',
      cancelButtonText: 'Cancelar'
    }).then((result) => {
      if (result.isConfirmed) {
        this.ubicacionService.eliminar(ubicacion).subscribe(resp => {
          Swal.fire({
            confirmButtonColor: "#a90000",
            title: 'Eliminado',
            text: `El registro ha sido eliminado`,
            icon: 'success',
          });
          this.reload()
        },
          (err: any) => {
            Swal.fire({
              confirmButtonColor: "#a90000",
              title: 'Advertencia',
              text: "No puede ser eliminado",
              icon: 'warning',
            });
          });
      } else if (result.isDenied) {
        Swal.fire({
          confirmButtonColor: "#a90000",
          title: "Información",
          text: `Cambios no aplicados`,
          icon: "info",
        });
      }
    });
  }

  editarlo(seleccion: Ubicacion, modal: any) {
    this.ubicacionSeleccionada = seleccion;
    this.openModal(modal, false);

  }

  accion() {
    if (this.ubicacionSeleccionada != null) {
      this.editar();
    } else {
      this.guardar();
    }
  }


  guardar() {
    this.showLoading = true
    const edificio = this.formulario.get('edificio').value
    const nivel = this.formulario.get('nivel').value
    const espacio = this.formulario.get('espacio').value
    this.ubicacion = new Ubicacion(null, new Edificio(edificio, null, null), espacio, nivel)
    this.ubicacionService.guardar(this.ubicacion).subscribe((resp: any) => {
      this.showLoading = false
      if (resp.mensaje === "Error ubicacón existente") {
        Swal.fire({
          confirmButtonColor: "#a90000",
          title: "Advertencia",
          text: 'Registro existente',
          icon: "warning",
        });
      } else {
        if (resp) {
          this.showLoading = false
          Swal.fire({
            confirmButtonColor: "#a90000",
            title: 'Éxito',
            text: `Almacenado correctamente`,
            icon: 'success',
          });
          this.reload()
          this.modalService.dismissAll()
          this.formulario.reset()
        } else {
          this.showLoading = false
          Swal.fire({
            confirmButtonColor: "#a90000",
            title: 'Advertencia',
            text: `Ocurrió un problema`,
            icon: 'warning',
          });
        }
      }
    })
    return Object.values(this.formulario.controls)
      .forEach(control =>
        control.markAsTouched())
  }

  editar() {
    this.showLoading = true
    this.ubicacionSeleccionada.edificio.id = this.formulario.controls['edificio'].value
    this.ubicacionSeleccionada.espaciofisico = this.formulario.controls['espacio'].value
    this.ubicacionSeleccionada.nivel = this.formulario.controls['nivel'].value

    this.ubicacionService.editar(this.ubicacionSeleccionada).subscribe((resp: any) => {
      this.showLoading = false
      if (resp.mensaje === "Error ubicacón existente") {
        Swal.fire({
          confirmButtonColor: "#a90000",
          title: "Advertencia",
          text: 'Registro existente',
          icon: "warning",
        });
      } else {
        if (resp) {
          this.showLoading = false
          Swal.fire({
            confirmButtonColor: "#a90000",
            title: 'Éxito',
            text: `Modificado correctamente`,
            icon: 'success',
          });
          this.reload()
          this.modalService.dismissAll()
          this.formulario.reset()
        } else {
          this.showLoading = false
          Swal.fire({
            confirmButtonColor: "#a90000",
            title: 'Advertencia',
            text: `Ocurrió un problema`,
            icon: 'warning',
          });
        }
      }
    }, (err) => {
      this.showLoading = false
      Swal.fire({
        confirmButtonColor: "#a90000",
        title: 'Error',
        text: `Algo fallo`,
        icon: 'error',
      });
    })
  }

  filtrarNivel() {
    const edificio = this.formulario.controls['edificio'].value;
    this.edificioService.edificio(edificio).subscribe((resp: any) => {
      let nivel: number = 0
      nivel = resp.niveles
      this.llenarNivel(nivel)

    });


  }

  llenarNivel(nivel: number) {

    this.niveles = [];
    for (let i = 1; i <= nivel; i++) {
      this.niveles.push(new Nivel(i, 'Nivel ' + i))
    }
  }

  llenarEdificio() {
    this.edificioService.edificios().subscribe((resp: any) => {
      this.edificios = resp;
      this.edificios.sort((a, b) => a.edificio.localeCompare(b.edificio));
    });
  }

  openModalA(content) {
    this.modalService.open(content, { keyboard: false, scrollable: true, size: 'lg' });
  }
}
